import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder,FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AzureService } from 'src/app/shared/services/azure.service';
import { MatDialog } from '@angular/material';
import { DeleteComponent } from '../delete/delete.component';

@Component({
  selector: 'app-azure-info',
  templateUrl: './azure-info.component.html',
  styleUrls: ['./azure-info.component.scss']
})
export class AzureInfoComponent implements OnInit {
 azureForm: FormGroup;
 public breakpoint: number; 
 wasFormChanged = false;



  constructor(private _formBuilder:FormBuilder, private_router:Router, private _azureService:AzureService, public dialog: MatDialog) { 
    this.azureForm = this._formBuilder.group({
      'clientID': ['',Validators.required],
      'subscriptionID':['',Validators.required],
      'tenantID':['',Validators.required],
      'clientSecret':['',Validators.required],
      'VMusername':['',Validators.required],
      'VMpassword': ['',[Validators.required, this.checkPassword]],
      'location':['',Validators.required],
      'prefix': ['',Validators.required],
      'VMsize': ['',Validators.required],
      'VMnumber': ['',[Validators.required, Validators.pattern("^[0-9]*$") ]],
      'offer': ['',Validators.required],
      'sku': ['',Validators.required],
      'version':  ['',Validators.required],
    });
    this.breakpoint = window.innerWidth <= 600 ? 1 : 3; // Breakpoint observer code

  
  }
  get f(){return this.azureForm.controls;}
  checkPassword(control) {
    let enteredPassword = control.value
    let passwordCheck = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.{8,})/;
    return (!passwordCheck.test(enteredPassword) && enteredPassword) ? { 'requirements': true } : null;
  }

  // tslint:disable-next-line:no-any
  public onResize(event: any): void {
    this.breakpoint = event.target.innerWidth <= 600 ? 1 : 2;
  }

  ngOnInit() {}

  public onAdd(): void {
    this.markAsDirty(this.azureForm);
  }

  openDialog(): void {
    console.log(this.wasFormChanged);
    if(this.azureForm.dirty) {
      const dialogRef = this.dialog.open(DeleteComponent, {
        width: '340px',
      });
    } else {
      this.dialog.closeAll();
    }
  }


  private markAsDirty(group: FormGroup): void {
    group.markAsDirty();
    // tslint:disable-next-line:forin
    for (const i in group.controls) {
      group.controls[i].markAsDirty();
    }
  }

  formChanged() {
    this.wasFormChanged = true;
  }

}
